import InnerContents from './innerContents';
import {scrollMove} from '../../lib/funcLibrary';

const background = (props) => {
  const {movieList} = props;
  
  const backgroundImgs = movieList.movie_img ? movieList.movie_img.map((el, idx) => 
    <div key={idx} style={{backgroundImage: `linear-gradient(rgba(0, 0, 0, 0.63), rgba(0, 0, 0, 0.63)), url(${el})`}} className="backgroundImg innerContentCenter">
      <InnerContents contentTitle={movieList.contentTitle[idx]} contentSubTitle={movieList.contentSubTitle[idx]} />
      <div onClick={() => scrollMove('next')} className={`arrowBtn ${movieList.movie_img.length - 1 === idx ? "lastArrow": "arrowDir"}`}></div>
    </div>
  ) : "please wait";

  return (
    <div>{backgroundImgs}</div>
  )
}
export default background;